.contact-us {
  &__info {
    margin: 0 0 24px;

    &--last {
      margin-bottom: 32px;
    }
  }

  &__info-title {
    margin: 0 0 8px;
    font-weight: 400;
    font-size: 14px;
    line-height: 27px;
    color: $contact-us--color-info;
  }
}

.contact-us__info-link {
  color: inherit;
  text-decoration: none;
  transition: $transition;

  &:hover {
    color: $color-blue;
  }
}

.contact-us__field {
  margin: 0 0 16px;

  &--last {
    margin-bottom: 32px;
  }
}
